// import logo from './logo.svg';
import './App.css';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import React, { useState} from 'react'
// æˆ–
//import { Input } from '@mui/material';
const api_base_url = ' http://ls44982hw6.herokuapp.com/api'


function App() {
  const [firstname, setFirstname] = useState('');
  function find() {
    var url_req_string = api_base_url+ "?firstname=" +firstname;//+ firstname;   
    console.log(url_req_string);
    fetch(url_req_string)
          .then(res => res.json())
          .then(
            (result) => {
              console.log(result);
            },
            (error) => {
              console.log(error);
            }
          );
  }
  return (
    <div className="App">
     <header className="App-header">
        {/*  <img src={logo} className="App-logo" alt="logo" /> */}
        <TextField id="standard-basic" label="Please input first name" variant="standard"  value={firstname} onChange={(event) => {setFirstname(event.target.value)}}/>
        <Button variant="contained" sx={{marginTop:10}} onClick={find}>Find Last Name</Button>
        
      </header>
    </div>
  );
}

export default App;
